body {
  background-color: #000000;
  font-family: "Inter", sans-serif;
  box-sizing: border-box;
}

h1 {
  margin: 0;
  font-size: 48px;
  line-height: 52px;
  color: white;
}

p {
  color: #888888;
  font-size: 16px;
  font-weight: 200;
  margin: 10px 0 0 0;
}

div {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  height: 100vh;
}